#portfolio {
  scroll-snap-type: y mandatory;
  overflow-y: auto;
  height: 100vh;
  width: 100vw;
}

#game-screen {
	background-color: black;
}

.title-block {
  height: calc(100vh - 100px);
  width: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  scroll-snap-align: start;
}

#intro {
  font-size: 1em;
  height: 100px;
  min-width: 500px;
  width: 75%;
  text-indent: 10px;
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
}

#name {
  font-size: 4em;
  font-weight: 600;
  min-width: 500px;
  width: 75%;
}

.main-nav {
  width: 100%;
  height: 100px;
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: flex-end;
  background-color: rgba(0,0,0,0);
  position: sticky;
  top: 0;
}

.nav-element {
	padding-right: 75px;
}

.about-section {
	padding-top: 150px;
	height: 1000px;
}

.skills-section {
	height: 1000px;
}

.info-block {
	scroll-snap-align: start;
}