body {
  margin: 0;
  font-family: 'Work Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #181818;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#gameContainer {
  background-color: black !important;
}

#game-page {
	transform: scale(.33);
	min-width: 540px;
	min-height: 540px;
	height: 100vh;
	width: 100vw;
	display: flex;
	flex-flow: column;
	justify-content: center;
	align-items: center;
}

.toggle-about {
	position: fixed;
	top: 0;
	height: 50px;
	width: 100%;
	border: none;
	display: flex;
	flex-flow: column;
	justify-content: flex-end;
	align-items: center;
	font-size: 1em;
	color: rgba(216,235,255,.25);
	z-index: 3;
	-webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -o-transition: 0.25s;
    transition: 0.25s;
    cursor: pointer;
    background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0));
    font-weight: bold;
}

.toggle-about.highlight {
	color: rgba(216,235,255,1);
	font-size: 1.25em;
}

.toggle-about.show {
	top: calc(100% - 50px);
	flex-flow: column-reverse;
	justify-content: flex-end;
}

.about {
	position: fixed;
	right: 0;
	height: 0%;
	width: 100%;
	background-color: rgba(10, 10, 10, .9);
	visibility: hidden;
	opacity: 0;
	z-index: 2;
	-webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -o-transition: 0.25s;
    transition: 0.25s;
}

.about.show {
	height: 100%;
	width: 100%;
	visibility:  visible;
	opacity: 1;
}


.about-container {
	margin-right: auto;
	margin-left: auto;
	max-width: 750px;
	width: 100%;
	height: calc(100% - 75px);
	display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: center;
    color: rgba(216,235,255,1);
}

.option-select {
	width: 100%;
	height: 75px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.option-display.selected {
	opacity: 1;
	display: block;
}

.option-display {
	width: 80%;
	flex-grow: 1;
	opacity: 0;
	-webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -o-transition: 0.25s;
    transition: 0.25s;
    display: none;
    margin-top: 20px;
    overflow-y: auto;
    padding: 0 10% 0 10%;
}

/* width */
.option-display::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.option-display::-webkit-scrollbar-track {
  background: rgba(0,0,0,0);
}

/* Handle */
.option-display::-webkit-scrollbar-thumb {
  background: rgba(216,235,255,.1);;
}

/* Handle on hover */
.option-display::-webkit-scrollbar-thumb:hover {
  background: rgba(216,235,255,.25);;
}

.option {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 33%;
	border-bottom: solid 1px rgba(216,235,255,0);
	font-size: 1em;
	color: rgba(216,235,255,.25);
	-webkit-transition: 0.25s;
    -moz-transition: 0.25s;
    -o-transition: 0.25s;
    transition: 0.25s;
    cursor: pointer;
}

.option.selected {
	border-bottom: solid 1px rgba(216,235,255,1);
	color: rgba(216,235,255,1);
	cursor: default;
}

.option.hover {
	color: rgba(216,235,255,1);
}

.toggle {
	width: 100%;
}

.arrow {
  transition: transform 150ms ease;
  box-sizing: border-box;
  height: 10px;
  width: 10px;
  border-style: solid;
  border-color: (216,235,255,.25);
  border-width: 0px 2px 2px 0px;
  transform: rotate(45deg);
}

.arrow.highlight {
  border-color: (216,235,255,1);
}

.arrow.show {
	transition: transform 150ms ease;
	transform: rotate(225deg);
}

.display-body {
	text-indent: 30px;
	line-height: 1.25;
	color: rgba(216,235,255,.5);
}

ul {
	color: rgba(216,235,255,.5);
}

ul b {
	color: rgba(216,235,255,1);;
}